
$(document).ready(function(){

	$(window).load(function(){
		AOS.init();
	});
	

	// (awards-and-accolades)
	//two tabs on one page with only one content
	// no more tab. only modal. comment out codes are not needed.
	//initiate
	$('.content #main-content2').hide(); //show first of two content
	$('ul.awards li.uk-active').addClass('initial'); //remove active class of second tab

	$('ul.awards a').click(function(){
		$('ul.awards li').removeClass('initial');
		$('.accollades li').removeClass('uk-active');
		// $('.content .uk-grid-small.uk-grid').hide();
		// $('.content #main-content2').fadeIn('slow');
	});

	$('ul.accollades').on('click', function(){
		$('.awards li').removeClass('uk-active');
		// $('.content #main-content2').hide();
		// $('.content .uk-grid-small.uk-grid').fadeIn('slow');
	});
	$('ul.accollades li').on('click', function(){
		$('ul.accollades li').removeClass('uk-active');
		$(this).addClass('uk-active');
	});
	//
	$('.accollades li').hover(function(){ 
		var $key = $(this).data('key'); 
    	$('.accolade-content').find("[data-content='" + $key + "']").toggleClass('active');
    });    

	//(our-partners)

	$('.slide .lawyer-contents').slick({
		slidesToShow: 1,
		slidesToScroll: 1,
		arrows: false,
		fade: true,
		adaptiveHeight: true,
		asNavFor: '.lawyers'
	});

	$('.slide .lawyers').slick({
	  slidesToShow: 45,
	  slidesToScroll: 1,
	  asNavFor: '.lawyer-contents',
	  vertical: true,
	  dots: false,
	  arrows: false,
	  focusOnSelect: true
	});
	
	
	
	//news
	$('div.year-details').slick({
	  slidesToShow: 1,
	  slidesToScroll: 1,
	  arrows: false,
	  fade: true,
	  asNavFor: 'div.year',
	});
	$('div.year').slick({
	  slidesToShow: 10,
	  slidesToScroll: 1,
	  asNavFor: 'div.year-details',
	  dots: false,
	  focusOnSelect: true,
	  prevArrow: '<i class="fa fa-chevron-left slick-prev" aria-hidden="true"></i>',
	  nextArrow: '<i class="fa fa-chevron-right slick-next" aria-hidden="true"></i>',
	   responsive: [
	    {
	      breakpoint: 768,
	      settings: {
	        slidesToShow: 8,
	        arrows: false,
	      }
	    },
	    {
	      breakpoint: 600,
	      settings: {
	        slidesToShow: 5,
	        arrows: false,
	      }
	    }
	  ]
	});

	//(principal-contacts)
	//isotope

	var $grid = $('.isotope').isotope({
		filter : '.all-none',
	});

	$('select.categories').change(function() {
	  var filterValue = $(this).find(':selected').attr('data-filter');
	  $grid.isotope({ filter: filterValue });
	});

	//(about-the-firm)

	$('.page-template-template-about .read-more').click(function () {
      $(this).text(function(i, text){
          return text === "View Less" ? "Read More" : "View Less";
      });
    });
	// CODES THAT INCLUDE ON LOAD AND RESIZE AT THE SAME TIME
	var resizeTimer;
	$(window).on('load resize', function(e){
		clearTimeout(resizeTimer);

		resizeTimer = setTimeout(function() {
			// Run code here, resizing has "stopped"

			var $pageHeight = $(window).height(),
			$pageWidth = $(window).width(),
			$navHeight = $('header.main-header').outerHeight(),
			$footerHeight = $('footer.footer').outerHeight(),
			$mainWrapper = $('.wrapper-holder');

			if( $mainWrapper.hasClass('not-sticky') ) {
				$('.wrapper-holder').css({
					'min-height': $pageHeight - $navHeight,
					'padding-bottom': $footerHeight
				});
			} else {
				$('.wrapper-holder').css({
					'min-height': $pageHeight - $navHeight,
					'padding-bottom': $footerHeight
				});
			}

			$('body.home main .wrapper-holder').css({
				'margin-top': 0,
				'height': $pageHeight,
				'min-height': 0,
			});

			/* MODERNIZR LAYOUT - This serves as the the media query inside the Javascript */
			if( Modernizr.mq('(min-width: 1200px)') ) {
				
			}
			else if( Modernizr.mq('(min-width: 992px)') && Modernizr.mq('(max-width: 1199px)') ) {
				// CODES FOR MEDIUM DEVICES HERE
				
			}
			else if( Modernizr.mq('(max-width: 991px)') && Modernizr.mq('(min-width: 768px)')){
				// CODES FOR SMALL DEVICES HERE

			}
			else{
				// CODES FOR EXTRA SMALL DEVICES HERE

			}

		}, 250);

	});
});